.header {
  background-color: #5a47a7;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  transition: 0.7s;
  z-index: 1000;
  box-shadow: 0 10px 100px rgba(0, 0, 0, 0.1);
}
.header.barra-color {
  background-color: rgba(86, 86, 86, 0.3294117647);
}
.header__barra {
  display: flex;
  flex-direction: column;
  margin: 0 2rem;
}
@media (min-width: 768px) {
  .header__barra {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}
.header__logo {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 1rem;
}
@media (min-width: 768px) {
  .header__logo {
    margin: 0;
  }
}
.header__logo img {
  max-width: 20%;
}
@media (min-width: 480px) {
  .header__logo img {
    max-width: 13%;
  }
}
@media (min-width: 768px) {
  .header__logo img {
    max-width: 50%;
  }
}
@media (min-width: 1024px) {
  .header__logo img {
    max-width: 50%;
  }
}
.header__nav-principal {
  display: flex;
  flex-direction: column;
  margin-top: 1rem;
}
@media (min-width: 768px) {
  .header__nav-principal {
    flex-direction: row;
    gap: 3rem;
    margin-top: 0;
  }
}
.header__nav-principal a {
  text-align: center;
  color: #ffffff;
  font-size: 1.3rem;
  text-transform: uppercase;
  font-weight: 700;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .header__nav-principal a {
    margin-bottom: 0;
  }
}
.header__nav-principal a.activo {
  color: #baff60;
}
.header__nav-principal a:hover {
  color: #baff60;
}

