  border-bottom: #dddddd solid 1px;
  background: linear-gradient(to right, rgba(245, 245, 245, 0.6), rgba(245, 245, 245, 0.6)), url(https://d33wubrfki0l68.cloudfront.net/daac8858fd4a0ccea44f59dfd079c3c16c263f33/c157c/assets/svg/common-bg.svg);
