    border-left: #5a47a7 solid 8px;
  }
}
.presentacion__heading {
  font-size: 2.2rem;
  letter-spacing: 2px;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .presentacion__heading {
    font-size: 2.5rem;
  }
}
@media (min-width: 1024px) {
  .presentacion__heading {
    font-size: 2.5rem;
  }
}
.presentacion__heading span {
  color: #5a47a7;
}
.presentacion__texto {
  text-transform: uppercase;
  color: #555;
  margin: 3rem 0;
