}

.frase::after {
  content: "";
  position: absolute;
  top: calc(100% + 1.5rem);
  height: 5px;
  width: 3rem;
  background: #5a47a7;
  left: 50%;
  transform: translateX(-50%);
  border-radius: 5px;
}

